.toolbar{
    position: fixed;
    top: 0;
    left: 0; 
    width: 100%;
    background: rgb(46, 46, 46);
    height: 75px;
    z-index: 9999;
}
.toolbar_navigation{
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1rem;
}
.toolbar_name{
    margin-left: 1rem;
    display: flex;
    flex-direction: column;
}
.name{
    color: white;
    text-decoration: none;
    font-size: 3rem;
    font-weight: bolder;
    font-family: "Lucida Console", Courier, monospace;
}
.profession{
    color: white;
    text-decoration: none;
    font-size: 1rem;
    font-weight: lighter;
    font-family: "Lucida Console", Courier, monospace;
    padding: 0 0.2rem;
}
.spacer{
    flex: 1;
}
/* //newly -----------------------------------------------------------------*/
.toolbar_navigation-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.toolbar_navigation-items li{
    padding: 0 10px;
}
.toolbar_navigation-items span{
    color: white;
    text-decoration: none;
    font-weight: lighter;
    font-size: 18px;
}
.toolbar_navigation-items span:hover,
.toolbar_navigation-items span:active{
    color: rgb(209, 107, 12);
    cursor: pointer;
    background-color: rgb(59, 59, 59);
    border-radius: 2px;
    padding: 0.5rem 0.5rem;
}

@media (max-width: 799px){
    .toolbar_navigation-items{
        display: none;
    }
}

@media (min-width: 800px){
    .toolbar__toggle-button{
        display: none;
    }
}
@media screen and (max-width: 480px){
    .name{
        font-size: 1.5rem;
        font-weight: bold;
    }
    .profession{
        font-size: 0.5rem;
        font-weight: lighter;
    }
}

.toolbar_navigation-items ul > .active{
    background-color: rgb(59, 59, 59);
    border-bottom: 2px solid rgb(209, 107, 12);
    border-radius: 2px;
    padding: 2px;
}