.side-drawer{
    height: 100%;
    background: white;
    box-shadow: 2px 0px 5px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    right: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(100%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open{
    transform: translateX(0);
}

.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
    padding: 1rem 0;
}

.side-drawer span {
    color: brown;
    text-decoration: none;
    font-size:2rem;
}
.side-drawer span:hover,
.side-drawer span:active{
    color: rgb(199, 74, 25);
    background-color: grey;
    border-radius: 2px;
    padding: 0.5rem 0.5rem;
    cursor: pointer;
}


@media (min-width: 800px) {
    .side-drawer{
        display: none;
    }
}