//left-side column
.work-text-box {
	position: relative;
    display: flex;
    margin-top: 30%;
	align-items: center;
	justify-content: center;
}

.work-h1 {
	font-size: 3.5vw;
	font-weight: 900;
	background-color: #12343b;
	color: #beef00;
	display: block;
    padding: .5em;
    text-transform: uppercase;
}

.work-h1:nth-child(2) {
	position: absolute;
	background-color: #beef00;
	color: #12343b;
	clip-path: inset(-1% -1% 45% -1%); // Added the negative values to prevent icky "bleed" in Chrome
}



// Codepen spesific styling - only to center the elements in the pen preview and viewport
.work-left-cont {
	position: relative;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}
// End Codepen spesific styling




//right-side-

$blue: #29b6f6;
$green: #9ccc65;
$purple: #ba68c8;
$orange: #f57c00;
$red: #ef5350;
$cyan: #4dd0e1;


@mixin text-color($color) {
  color: $color;
}

.blue {
  @include text-color($blue);
}

.green {
  @include text-color($green);
}

.purple {
  @include text-color($purple);
}

.cyan {
  @include text-color($cyan);
}

.red {
  @include text-color($red);
}

.workk-content{
  height: 100%;
  @include text-color(white);

  display: flex;
  flex-direction: column;
//   justify-content: center;
//   align-items: center;
}

.workk-card{
  //FUNCTIONALITY
  perspective: 150rem;
  height: 10rem;
  width: 35rem;
  margin: 1.5rem 1.5rem;

  position: relative;

  &__side {
    height: 10rem;
    transition: all 1.5s ease;

    position: absolute;
    top: 0;
    left: 0;
    margin: 0rem 0rem;
    width: 51rem;

    backface-visibility: hidden;
    border-radius: 3px;
    overflow: hidden;
    box-shadow: 0 1.5rem 4rem rgba(black, 0.4);

    &--front {
      background-color: #1c1c1c;
    }

    &--back {
      transform: rotateY(180deg);

      background-color: #4b4b4b;
    }
  }

  &:hover &__side--front {
    transform: rotateY(-180deg);
  }

  &:hover &__side--back {
    transform: rotateY(0deg);
  }

  //FRONT SIDE STYLING
  &__cont {
    height: 10rem;
    background-color: #4b4b4b;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__cta {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    @include text-color(white);

    p {
      margin-left: 1rem;

      & > .space {
        margin-left: 2rem;
      }
    }
  }
}


@media screen and (max-width: 480px) {
  .work-left-cont{
    top: 35%;
  }
  .workk-card{
    //FUNCTIONALITY
    perspective: 150rem;
    height: 10rem;
    width: 10rem;
    margin: 1.5rem 1.5rem;
    &__side {
      height: 10rem;
      transition: all 1.5s ease;
  
      position: absolute;
      top: 0;
      left: 0;
      margin: 0rem 0rem;
      width: 10rem;
    }
    &:hover &__side--back {
      transform: rotateY(0deg);
      font-size: 8px;
    }
  }
}