.main-toolbar{
    display: inline-flex;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    box-shadow: 1px 1px 1px #222;
    animation: moveDown 0.3s ease-in-out;
}
.main-toolbar-none{
    display: none;
}

@keyframes moveDown {
    from {
      transform: translateY(-5rem);
    }
    to {
      transform: translateY(0rem);
    }
  }