@import url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');

.footer{
    height: 9rem;
    width: 100%;
    background: rgb(153, 153, 153);
    position: relative;
    display: flex;
    flex-direction: column;
}
.footer-details{
    margin: auto;
    padding: 20px;
    width:50%;
    height: 2rem;
    text-align: center;
}
.footer-name{
    font-size: 20px;
    font-weight: normal;
    letter-spacing: 10px;
}
.footer-copyright{
    font-size: 13px;
    font-style: italic;
    font-weight: lighter;
}

.ul {
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    padding: 0;
    display: flex;
}
.ul .li {
    position: relative;
    list-style: none;
    margin: 0rem 50px;
}
@media screen and (min-width: 421px) and (max-width: 720px){
    .ul .li{
        margin: 0rem 15px;
    }
    .footer-name{
        font-size: 15px;
    }
    .footer-copyright{
        font-size: 10px;
    }
}
@media screen and (max-width: 420px){
    .ul .li{
        margin: 0rem 5px;
    }
    .footer-details{
        width: 80%;
    }
    .footer-name{
        font-size: 10px;
    }
    .footer-copyright{
        font-size: 8px;
    }
}
.ul .li::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -4px;
    width: 40px;
    height: 8px;
    background: #000;
    border-radius: 50%;
    transition: 0.5s;
    opacity: 0;
    filter: blur(2px);
    transform: scale(0.8);
}
.ul .li:hover::before {
    transition-delay: 0.2s;
    opacity: 0.2;
    transform: scale(1);
}
.ul .li .a {
    width: 40px;
    height: 40px;
    display: block;
    transition: 0.5s;
}
.ul .li:hover .a {
    transform: translateY(-10px);
}
.ul .li .a .span  {
    width: 100%;
    height: 100%;
}
.ul .li .a .span::before {
    font-family: fontAwesome;
    text-align: center;
    line-height: 40px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    color: #262626;
    transform-origin: top;
    transition: transform 0.5s;
}
.ul .li:hover .a .span::before {
    transform: rotateX(90deg) translateY(-50%);
}
.ul .li .a .span::after {
    font-family: fontAwesome;
    text-align: center;
    line-height: 40px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: #fff;
    color: #262626; */
    transform-origin: bottom;
    transition: transform 0.5s;
    transform: rotateX(90deg) translateY(50%);
}
.ul .li:hover .a .span::after {
    transform: rotateX(0deg) translateY(0);
}
.ul .li:nth-child(1) .a .span::before,
.ul .li:nth-child(1) .a .span::after {
    content: '\f082';
}
.ul .li:nth-child(2) .a .span::before,
.ul .li:nth-child(2) .a .span::after {
    content: '\f099';
}
.ul .li:nth-child(3) .a .span::before,
.ul .li:nth-child(3) .a .span::after {
    content: '\f09b';
}
.ul .li:nth-child(4) .a .span::before,
.ul .li:nth-child(4) .a .span::after {
    content: '\f0e1';
}
.ul .li:nth-child(5) .a .span::before,
.ul .li:nth-child(5) .a .span::after {
    content: '\f16d';
}
.ul .li:nth-child(1) .a .span::after {
    background: #3b5999;
    color: #fff;
}
.ul .li:nth-child(2) .a .span::after {
    background: #55acce;
    color: #fff;
}
.ul .li:nth-child(3) .a .span::after {
    background: #24292e;
    color: #fff;
}
.ul .li:nth-child(4) .a .span::after {
    background: #0077b5;
    color: #fff;
}
.ul .li:nth-child(5) .a .span::after {
    background: rgb(193,53,132);
    color: #fff;
}