@import url('https://fonts.googleapis.com/css?family=Montserrat:900i&display=swap');
.academics-main-cont{
    height: 90vh;
    width: 100%;
    display: flex;
    flex-direction: row;
	background: url('./aca-back.jfif');
	background-attachment: fixed;
}
//left-side
.academics-left-container{
    height: 100%;
    width: 40%;
	
	// background-attachment: fixed;
	font-family: 'Montserrat', sans-serif;
}
.aca-header{
    position: relative;
    top: 40%;
    left: 1%;
    font-size:80px;
    letter-spacing:0.1em;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 3px;
    -webkit-text-stroke-color: white;
    text-shadow: 6px 6px #ff1f8f, 15px 15px #202020;
  }

//right-side bar
@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');

.academics-right-container{
    height: 100%;
    width: 60%;
}

.course {
	background-color: rgb(216, 216, 216);
	border-radius: 10px;
	box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.2);
	display: flex;
	margin: 20px;
	overflow: hidden;
    width: 90%;
}

.course h5 {
    opacity: 0.8;
	margin: 0;
    letter-spacing: 1px;
    font-size: 15px;
    padding-bottom: 5px;
}
.course h6 {
	opacity: 0.6;
	margin: 0;
	letter-spacing: 1px;
	text-transform: uppercase;
}

.course h2 {
	letter-spacing: 1px;
	margin: 10px 0;
}

.course-preview {
	background-color: #2A265F;
	color: #fff;
	padding: 30px;
	width: 250px;
}

.course-info {
	padding: 30px;
	position: relative;
	width: 100%;
}

.progress-container {
	position: absolute;
	top: 30px;
	right: 30px;
	text-align: right;
	width: 150px;
}

.progress {
	background-color: #ddd;
	border-radius: 3px;
	height: 5px;
	width: 100%;
}

.progress::after {
	border-radius: 3px;
	background-color: #2A265F;
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	height: 5px;
	width: 100%;
}

.progress-text {
	font-size: 10px;
	opacity: 0.6;
	letter-spacing: 1px;
}

@media screen and (max-width: 480px){
	.aca-header{
		left: 3%;
		font-size:18px;
		-webkit-text-stroke-width: 1px;
		text-shadow: 3.5px 3.5px #ff1f8f, 7.5px 7.5px #202020;
	  }
	.course {
		border-radius: 10px;
		margin: 30px 10px;
	}
	.courses-container {
		padding-top: 20px;
	}
	.course h5 {
		font-size: 6px;
		padding-bottom: 2px;
	}
	.course h6 {
		font-size: 5px;
	}
	
	.course h2 {
		letter-spacing: 1px;
		font-size: 7px;
		margin: 10px 0;
	}
	
	.course-preview {
		padding: 10px;
		width: 80px;
	}
	
	.course-info {
		padding: 15px;
	}
	
	.progress-container {
		top: 10px;
		right: 5px;
		height: 10px;
		width: 30px;
	}
	
	.progress {
		height: 0px;
	}
	
	.progress-text {
		display: none;
	}
}