.about-main-container{
    height: 90vh;
    width: 100%;
    display: flex;
    flex-direction: row;

    background-color: #141114;
  background-image: linear-gradient(335deg, black 23px, transparent 23px),
    linear-gradient(155deg, black 23px, transparent 23px),
    linear-gradient(335deg, black 23px, transparent 23px),
    linear-gradient(155deg, black 23px, transparent 23px);
    background-size: 58px 58px;
  background-position: 0px 2px, 4px 35px, 29px 31px, 34px 6px;
  background-attachment: fixed;
}
.about-left-container{
    height: 100%;
    width: 60%;
    // background: rgb(216, 216, 216);
}
@import url("https://fonts.googleapis.com/css?family=Roboto:400,400i,700");
//left side
.description-about-me{
    font-family: "Comic Sans MS", cursive, sans-serif;
    font-size: 35px;
    padding: 0px 30px;
    font-style: italic;
    font-weight: bold;
    color: rgb(68, 68, 68);
}

.underlined {
    color: white;
    line-height: 1.5;
    text-decoration: none;
    background-image: linear-gradient(to right, rgb(138, 184, 63) 0, yellow 100%);
    background-position: 0 1em;
    background-size: 0 100%;
    padding: 1rem;
    background-repeat: no-repeat;
    &:hover {
      background-size: 100% 100%;
      background-position: 0 0.2em;
    }
    &--gradient {
      background-position: 100% -0.1em;
      background-image: linear-gradient(to right, yellow 0, lightgreen 100%);
    }
    &--reverse {
      background-position: 100% -0.1em;
      background-image: linear-gradient(to right, rgb(252, 252, 69) 0, rgb(125, 170, 19) 100%);
    }
  }

  
  



