    flex-direction: column; */
}
.toolbar{
    position: fixed;
    top: 0;
    left: 0; 
    width: 100%;
    background: rgb(46, 46, 46);
    height: 75px;
    z-index: 9999;
}
.toolbar_navigation{
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1rem;
}
.toolbar_name{
    margin-left: 1rem;
    display: flex;
    flex-direction: column;
}
.name{
    color: white;
    text-decoration: none;
    font-size: 3rem;
    font-weight: bolder;
    font-family: "Lucida Console", Courier, monospace;
}
.profession{
    color: white;
    text-decoration: none;
    font-size: 1rem;
    font-weight: lighter;
    font-family: "Lucida Console", Courier, monospace;
    padding: 0 0.2rem;
}
.spacer{
    flex: 1 1;
}
/* //newly -----------------------------------------------------------------*/
.toolbar_navigation-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.toolbar_navigation-items li{
    padding: 0 10px;
}
.toolbar_navigation-items span{
    color: white;
    text-decoration: none;
    font-weight: lighter;
    font-size: 18px;
}
.toolbar_navigation-items span:hover,
.toolbar_navigation-items span:active{
    color: rgb(209, 107, 12);
    cursor: pointer;
    background-color: rgb(59, 59, 59);
    border-radius: 2px;
    padding: 0.5rem 0.5rem;
}

@media (max-width: 799px){
    .toolbar_navigation-items{
        display: none;
    }
}

@media (min-width: 800px){
    .toolbar__toggle-button{
        display: none;
    }
}
@media screen and (max-width: 480px){
    .name{
        font-size: 1.5rem;
        font-weight: bold;
    }
    .profession{
        font-size: 0.5rem;
        font-weight: lighter;
    }
}

.toolbar_navigation-items ul > .active{
    background-color: rgb(59, 59, 59);
    border-bottom: 2px solid rgb(209, 107, 12);
    border-radius: 2px;
    padding: 2px;
}
.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background:transparent;
    border:none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}
.toggle-button:focus{
    outline: none;
}
.toggle-button_line{
    width: 30px;
    height: 2px;
    background: white;
}
.side-drawer{
    height: 100%;
    background: white;
    box-shadow: 2px 0px 5px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    right: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(100%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open{
    transform: translateX(0);
}

.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
    padding: 1rem 0;
}

.side-drawer span {
    color: brown;
    text-decoration: none;
    font-size:2rem;
}
.side-drawer span:hover,
.side-drawer span:active{
    color: rgb(199, 74, 25);
    background-color: grey;
    border-radius: 2px;
    padding: 0.5rem 0.5rem;
    cursor: pointer;
}


@media (min-width: 800px) {
    .side-drawer{
        display: none;
    }
}
.backdrop{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
.main-toolbar{
    display: inline-flex;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    box-shadow: 1px 1px 1px #222;
    -webkit-animation: moveDown 0.3s ease-in-out;
            animation: moveDown 0.3s ease-in-out;
}
.main-toolbar-none{
    display: none;
}

@-webkit-keyframes moveDown {
    from {
      transform: translateY(-5rem);
    }
    to {
      transform: translateY(0rem);
    }
  }

@keyframes moveDown {
    from {
      transform: translateY(-5rem);
    }
    to {
      transform: translateY(0rem);
    }
  }
.footer{
    height: 9rem;
    width: 100%;
    background: rgb(153, 153, 153);
    position: relative;
    display: flex;
    flex-direction: column;
}
.footer-details{
    margin: auto;
    padding: 20px;
    width:50%;
    height: 2rem;
    text-align: center;
}
.footer-name{
    font-size: 20px;
    font-weight: normal;
    letter-spacing: 10px;
}
.footer-copyright{
    font-size: 13px;
    font-style: italic;
    font-weight: lighter;
}

.ul {
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    padding: 0;
    display: flex;
}
.ul .li {
    position: relative;
    list-style: none;
    margin: 0rem 50px;
}
@media screen and (min-width: 421px) and (max-width: 720px){
    .ul .li{
        margin: 0rem 15px;
    }
    .footer-name{
        font-size: 15px;
    }
    .footer-copyright{
        font-size: 10px;
    }
}
@media screen and (max-width: 420px){
    .ul .li{
        margin: 0rem 5px;
    }
    .footer-details{
        width: 80%;
    }
    .footer-name{
        font-size: 10px;
    }
    .footer-copyright{
        font-size: 8px;
    }
}
.ul .li::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -4px;
    width: 40px;
    height: 8px;
    background: #000;
    border-radius: 50%;
    transition: 0.5s;
    opacity: 0;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    transform: scale(0.8);
}
.ul .li:hover::before {
    transition-delay: 0.2s;
    opacity: 0.2;
    transform: scale(1);
}
.ul .li .a {
    width: 40px;
    height: 40px;
    display: block;
    transition: 0.5s;
}
.ul .li:hover .a {
    transform: translateY(-10px);
}
.ul .li .a .span  {
    width: 100%;
    height: 100%;
}
.ul .li .a .span::before {
    font-family: fontAwesome;
    text-align: center;
    line-height: 40px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    color: #262626;
    transform-origin: top;
    transition: transform 0.5s;
}
.ul .li:hover .a .span::before {
    transform: rotateX(90deg) translateY(-50%);
}
.ul .li .a .span::after {
    font-family: fontAwesome;
    text-align: center;
    line-height: 40px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: #fff;
    color: #262626; */
    transform-origin: bottom;
    transition: transform 0.5s;
    transform: rotateX(90deg) translateY(50%);
}
.ul .li:hover .a .span::after {
    transform: rotateX(0deg) translateY(0);
}
.ul .li:nth-child(1) .a .span::before,
.ul .li:nth-child(1) .a .span::after {
    content: '\f082';
}
.ul .li:nth-child(2) .a .span::before,
.ul .li:nth-child(2) .a .span::after {
    content: '\f099';
}
.ul .li:nth-child(3) .a .span::before,
.ul .li:nth-child(3) .a .span::after {
    content: '\f09b';
}
.ul .li:nth-child(4) .a .span::before,
.ul .li:nth-child(4) .a .span::after {
    content: '\f0e1';
}
.ul .li:nth-child(5) .a .span::before,
.ul .li:nth-child(5) .a .span::after {
    content: '\f16d';
}
.ul .li:nth-child(1) .a .span::after {
    background: #3b5999;
    color: #fff;
}
.ul .li:nth-child(2) .a .span::after {
    background: #55acce;
    color: #fff;
}
.ul .li:nth-child(3) .a .span::after {
    background: #24292e;
    color: #fff;
}
.ul .li:nth-child(4) .a .span::after {
    background: #0077b5;
    color: #fff;
}
.ul .li:nth-child(5) .a .span::after {
    background: rgb(193,53,132);
    color: #fff;
}
